@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap);
@media screen and (min-width: 601px) {
  html {
    font-size: 18px;
  }
}

@media screen and (max-width: 600px) {
  html {
    font-size: 10px;
  }
}

body {
  font-family: "Noto Sans TC", sans-serif !important;
  
}



.navbar-outer {
  position: absolute;
  z-index: 20;
  left: 0px;
  top: 0px;

  width: 100%;
  height: 5rem;

  padding: 0.5rem 1rem 0 1rem;

  transition: 0.35s;
}

.navMove {
  background-color: #FFFFFF;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);

  position: fixed;

  transition: 0.35s;
}

.navbar-outer .navbar-inner{
  position: relative;

  width: 100%;
  height: 100%;

  background-color: transparent;
  
  display: flex;
  flex-direction: row;
  align-items: center;
}

.navbarBrand{
  width: 30%;
  height: 100%;

  margin-left: 1rem;
}

.navbarBrand-inner {
  display: flex;
  justify-items: start;
  align-items: center;

  width: 100%;
  height: 100%;

  color: #403e3e;
}

.navbarBrand .brandName {
  font-size: 1.5rem;
  letter-spacing: 0.3rem;
  font-weight: 500;

  margin: 0;
}

.navbarBrand .brandImg {
  height: 3rem;
  width: 3rem;
  
}

.nav .nav-link{
  color:#403e3e
}

.cover {
  width: Calc(100% - 2rem);
  height: Calc(100vh - 2rem);

  border-radius: 5px;
  background-color: #f0f0f0;

  margin: 1rem;

  position: relative;

  
}

.cover .cover-inner {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;

  padding-left: 10rem;
}

.cover .title {
  font-size: 3rem;
  letter-spacing: 1rem;
  line-height: 4rem;
  font-weight: 600;

  color:#403e3e;

  
}

.cover .subtitle {
  font-size: 2rem;
  letter-spacing: 0.8rem;
  font-weight: 500;

  color:#403e3e;
}

.cover .down {
  font-size: 3rem;

  color:#403e3e;


  position: absolute;
  bottom: 1rem;
  left: Calc(50% - 1.5rem);
}



.pageTitle {
  width: Calc(100%-2rem);
  height: 15vh;

  display: flex;

  margin-left: 0.5rem;
  margin-right: 0.5rem;
  margin-top: 0rem;
  margin-bottom: 1rem;

  position: relative;
}

.pageTitle .block{
  
  background-color: #FFFFFF;

  border-radius: 5px;

  height: 100%;
  width:25%;

  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.pageTitle .titleBlock{

  background-color: #f0f0f0;

  border-radius: 5px;

  height: 100%;
  width: 50%;

  margin-left: 0.5rem;
  margin-right: 0.5rem;
  

  display: flex;
  justify-content: center;
  align-items: center;
  
  
}

.pageTitle .titleBlock p{
  color: #403E3E;

  font-size: 3rem;
  letter-spacing: 0.5rem;
  font-weight: 500;

  margin: 0px;  
}





.footer {
  width: 100%;
  height: 2rem;

  display: block;
  

  background-color: #ffffff;


  
  margin-top: 5rem;
}

.home .section {
  width: Calc(100% - 2rem);
  height: Calc(100vh - 2rem - 5rem);

  border-radius: 5px;
  background-color: #f0f0f0;

  margin: 1rem;

  position: relative;
}

.home .section-half {
  width: Calc(100% - 2rem);
  height: Calc(50vh - 1rem - 2.5rem);

  border-radius: 5px;
  background-color: #f0f0f0;

  margin: 1rem;

  position: relative;
}

.home .subTitle {
  
  font-size: 2.5rem;
  letter-spacing: 0.1rem;
  font-weight: 500;

  text-align: start;
  padding-left: 1rem;

  color: #FFFFFF;

  position: absolute;
}

.home .slogan {
  font-size: 2.6rem;
  letter-spacing: 0.5rem;
  font-weight: 500;

  color: #f0f0f0;

  width: Calc(100% - 4rem);
  height: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  margin-top: 1rem;
  
}

.home .section .about {
  height: 100%;
  width: 100%;
}

.home .section .about .card {
  height: 100%;
  width: 100%;

  background-color: transparent;
  border: 0;
}

.home .section .about .card .title {
  width: 100%;

  font-size: 2.6rem;
  letter-spacing: 0.3rem;
  font-weight: 500;
  line-height: 3.2rem;

  text-align: center;

  color: #F0F0F0;
}



.home .section .about .card .imagebox {
  /* margin-top: 3rem; */

  max-height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.home .section .joinUs .subSection {
  width: 100%;
  height: Calc(100vh - 2rem - 5rem);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  font-size: 2rem;
  line-height: 3.5rem;
}

.publications-card {
  margin-top: 1rem;

  width: 100%;
  min-height: 15rem;

  transition: 0.25s;

  border-radius: 5px;
  /* border-color: black;
    border-style: solid;
    border-width: 1.1px; */

  background-color: transparent;
  color: black;
}

.publications-card:hover {
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */

  background-color: #d0d0d0;
  color: black;

  transition: 0.25s;
}

.publications-card > .title {
  font-size: 1.8rem;
  letter-spacing: default;
  font-weight: 500;
  line-height: normal;

  margin-top: 0rem;
  margin-bottom: 0px;

  padding: 0.5rem;
}

.publications-card > .author {
  font-size: 1rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  margin-top: 0rem;
  margin-bottom: 0px;

  padding: 0.5rem;
}
.publications-card > .at {
  font-size: 1.2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  margin-top: 0rem;
  margin-bottom: 0px;

  padding: 0.5rem;
}


.research .introPage .projectName{
  display: flex;
  justify-content: center;
  align-items: center;

  height: 5rem;

  font-size: 2.5rem;
  letter-spacing: 0.3rem;
  font-weight: 600;
  line-height: normal;

}

.research .introPage .description{
  display: flex;
  flex-direction: column;

  
}

.research .introPage .description .title{
  

  font-size: 2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  margin-top: 1rem;
}

.research .introPage .description .text{
  margin-left: 1rem;

  font-size: 1.2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;
}

.research .introPage .image {
  display: flex;
  flex-direction: column;

}

.research .introPage .image .title{
  font-size: 2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  margin-top: 1rem;
}

.research .introPage .image img{
  max-width: 100%;

  margin: 0 1rem 0 1rem;
  
}

.research .introPage .image .text{
  font-size: 1.2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  text-align: center;

  margin-top: 1rem;
}

.principalInvestiagtorPage .name {
  font-size: 2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

}

.principalInvestiagtorPage .appellation {
  font-size: 1rem;
  letter-spacing: default;
  font-weight: 300;
  line-height: normal;

  margin-bottom: 0.3rem;
}

.principalInvestiagtorPage .title {
  font-size: 1.6rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  margin-top: 1rem;
}

.principalInvestiagtorPage .title p{
  margin-bottom: 0.8rem;
}

.principalInvestiagtorPage .text {
  font-size: 1rem;
  letter-spacing: default;
  font-weight: 300;
  line-height: normal;


  margin-left: 1rem;
}

.principalInvestiagtorPage .text p{
  margin-bottom: 0.5rem;
}

.contact .title {
  font-size: 1.6rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: 100%;
}

.contact .text{
  font-size: 1rem;
  letter-spacing: default;
  font-weight: 300;
  line-height: normal;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  height: 100%;

}

.contact .text .subtitle {
  font-size: 1.2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;
}

.honor {

}

.honor .section {
  width: 90%;
  margin-right: auto;
  margin-left: auto;

  margin-top: 4rem;
}

.honor .section .year {
  font-size: 2.5rem;
  letter-spacing: default;
  font-weight: 500;
  line-height: normal;

  text-align: center;
}

.honor .section .title {
  font-size: 1.5rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  text-align: center;
}

.honor .section .award {
  font-size: 2rem;
  letter-spacing: default;
  font-weight: 600;
  line-height: normal;

  text-align: center;
}
